	module.exports = {"data":{"post":{"html":"<p>Good news! Mama's fever is down, she and Christopher are feeling fine. If his bilirubin value keeps sinking, they can come home tomorrow or Monday. We'll know for sure around noon tomorrow.</p>\n<p>We got a nice New Year's present from the German government today... a gas tax increase of 7 Pf went into effect, and the oil companies were kind enough to add 4 Pf of their own. I filled up for DM 1,90 today. That's over a dollar per liter... over $4 per gallon.</p>","fields":{"title":"Christopher Ryan Hanson","date":"01 Jan 2000","url":"https://papascott.de/2000-01-01-christopher-ryan-hanson/"}}},"pathContext":{"slug":"/2000-01-01-christopher-ryan-hanson/","prev":{"fields":{"title":"Christopher Ryan Hanson","date":"31 Dec 1999","slug":"/1999-12-31-christopher-ryan-hanson/"}},"next":{"fields":{"title":"Christopher is home","date":"02 Jan 2000","slug":"/2000-01-02-christopher-is-home/"}}}}

